h3 {
    margin: 0;
    padding: 0;
}
  
.field {
  text-align: center;
  padding-top: 5%;
  margin-left: 15%;
  height: 100%;
  width: 87%;
}

.in {
  width: 60vw;
  height: 20vh;
  margin-left: 7%;
  margin-top: 3%;
}

.table {
  width: 80%;
  border-radius: 5px;
  border-collapse: collapse;
}
.table thead th {
  text-align: left;
  padding: 10px 15px;
}
.table tbody td {
  text-align: left;
  padding: 10px 15px;
  vertical-align: top;
  border-radius: 5px;
  background-color: rgb(255, 255, 255)
}

th {
  text-transform: uppercase;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  color: #3C5AA8;
  opacity: 0.4;
}

td {
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 16px;
  border-bottom: 4px solid rgb(248, 248, 248);
  margin: auto;
  color: #5D6E97;
}

.field-title {
  text-align: left;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
  color: #3C5AA8;
  opacity: 0.4;
}

.input-place{
  height: 5vh;
  margin-top: 5%;
  margin-right: 20%;
  text-align: center;
}

.input-id{
  width: 25%;
  height: 65%;
  font-size: 13px;
  padding: 6px 0 4px 10px;
  border: 1px solid #cecece;
  background: #F6F6f6;
  border-radius: 8px;
  margin-right: 10px;
}

.input-reg{
  width: 25%;
  height: 65%;
  font-size: 13px;
  padding: 6px 0 4px 10px;
  border: 1px solid #cecece;
  background: #F6F6f6;
  border-radius: 8px;
  margin-right: 10px;
}

.input-btn{
  height: 65%;
  width: 10%;
  font-weight: normal;
  font-style: normal;
  color: white;
  padding: 6px 10px 4px 10px;
  background: #4A9DFF;
  border: 0px;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
} 
.input-btn:hover { background: rgb(53, 102, 167); }
.input-btn:active {
  background: rgb(33, 50, 147);
  box-shadow: 0 3px rgb(50, 33, 147) inset;
}

.remove-btn{
  margin-left: 5%;
  font-weight: normal;
  font-style: normal;
  color: white;
  padding: 6px 10px 4px 10px;
  border-radius: 8px;
  border: 0px;
  background: #e24242;
  transition: 0.2s;
  cursor: pointer;
} 
.remove-btn:hover { background: rgb(211, 66, 66); }
.remove-btn:active {
  background: rgb(156, 18, 18); 
}

.edit-btn{
  font-weight: normal;
  font-style: normal;
  color: white;
  padding: 6px 10px 4px 10px;
  border-radius: 8px;
  border: 0px;
  background: #4A9DFF;
  transition: 0.2s;
  cursor: pointer;
} 
.edit-btn:hover { 
  background: rgb(53, 102, 167); 
}
.edit-btn:active {
  background: rgb(33, 50, 147); 
}

.save-btn{
  font-weight: normal;
  font-style: normal;
  color: white;
  padding: 6px 10px 4px 10px;
  border-radius: 8px;
  border: 0px;
  background: #44c438;
  transition: 0.2s;
  cursor: pointer;
} 
.save-btn:hover { background: rgb(55, 179, 65); }
.save-btn:active {
  background: rgb(22, 121, 13); 
}

.calculate-place{
  height: 5vh;
  margin-top: 1%;
  margin-right: 20%;
} 

.retention {
  float: left;
  margin-top: 0.5%;
  font-weight: normal;
  font-style: normal;
  color: #3C5AA8;
  opacity: 0.4;
}

.calculate {
  float: right;
  height: 70%;
  width: 20%;
  text-align: center;
  font-weight: normal;
  font-style: normal;
  color: white;
  border: 0px;
  padding: 6px 10px 4px 7px;
  background: #4A9DFF;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
} 
.calculate:hover { 
  background: rgb(53, 102, 167); 
}
.calculate:active {
  background: rgb(33, 50, 147);
  box-shadow: 0 3px rgb(50, 33, 147) inset;
}

.graph {
  width: 80%;
}

.graph-none {
  width: 80%;
  display: none;
}

.lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
}

.lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid #4A9DFF;
  border-color: #4A9DFF transparent #4A9DFF transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.change-dt {
  font-size: 13px;
  padding: 6px 0 4px 10px;
  border-radius: 8px;
  border: 0;
}  

.table-operations {
  margin-top: 5%;
  text-align: left;
  font-size: 13px;
  border: 1px solid #3C5AA8;
  border-radius: 5px;
}

.th-operations {
  font-size: 13px;
}