.menu {
    float: left;
    width: 13%;
    top: 8%;
    padding-bottom: 90%;
    position: fixed;
    height: 90%;
    background-color: rgb(255, 255, 255);
    box-shadow: 4px 0px 10px rgba(93, 109, 151, 0.1);
  }

  .menu-block {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    width: 100%;
    height: 8vh;
    font-size: 18px;
    
    padding-right: 20%;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 0.5px solid rgba(93, 110, 151, 0.4);
  }

  .menu-block-a {
    border-top: 0.5px solid rgba(93, 110, 151, 0.4);
  }

  .menu-block-projects {
    padding-right: 40%;
  }
  
  .img-arrow { 
    margin-right: 20px;
    transition: transform 1s ease;
  }
  .rotate {
    transform: rotate(180deg);
  }
  
  .menu-link {
    text-decoration: none;
    color: #5D6D97;
    cursor: pointer;
  }
  .menu-link:hover {
    text-decoration: underline;
  }
  
  .show {
    float: left;
    justify-content: center;
    align-items: center;

    width: 100%;
    font-size: 18px;
    
    padding-right: 35%;
    border: 0;
    color: #666666;
  }
  
  .hide {
    display: none;
  }

  li {
    margin-top: 10%;
    margin-left: 15%;
    font-size: 30px;
    white-space: nowrap;
  }

 .link-project {
    color: #666666;
    font-size: 18px;
  }

  .block-add {
    margin-top: 15%;
    margin-bottom: 20%;
  }

  .link-add {
    margin-left: 12%;
    color: #666666;
    white-space: nowrap;
    text-decoration: none;
    cursor: pointer;
  }
  .link-add:hover {
    text-decoration: underline;
  }

  .img-plus { 
    margin-right: 25px;
  }