.header{
    width: 100vw;
    height: 8vh; 
    text-align: center;
    background:  #f7f7f7;
    margin-bottom: 1px;
    box-shadow: 4px 5px 10px rgba(0, 0, 0, 0.1);
    position: fixed;
    overflow: auto;
    z-index: 9999;
  }
  
  .block-title {  
    position: relative;
    width: 10%;
    margin-left: 2%;
    margin-top: 0.5vh;
    font-size: 90%;
    color: #5D6E97;
  }
  
  .block-input { 
    position: relative;
    width: 15%;
    margin-top: 2.2vh;
    margin-left: 15%;
  }
  
  .block-photo { 
    width: 40px;
    height: 40px;
    margin-top: -1.7%;
    position: absolute;
    margin-left: 90%;
  }
  
  .block-exit { 
    position: absolute;
    margin-left: 95%;
    margin-top: -1.1%;
  }
  
  .title-text {
    position: absolute;
    font-weight: bold;
    font-style: normal;
    text-transform: uppercase;
  }
  
  .input-search[type="text"] {
    width: 15vw;
    height: 3vh;
    padding-left: 35px;
    
    background: #ffffff;
    border: 0px;
    border-radius: 20px;
  }
  .input-search:focus {
    outline: none;
  }
  
  .search {
    top: 20%;
    left: 5%;
    border-radius: 40%;
    position: absolute;
    cursor: pointer;
  }
  
  .search:hover {
    background-color: wheat;
  }
  
  .search:active {
    background-color: goldenrod;
  }
  
  .photo {
    width: 100%;
    height: 100%;
  
    background: url('/src/img/racoon.jpg');
    background-blend-mode: normal;
    border-radius: 50%;
  }
  
  .exit {
    left: 95.58%;
    cursor: pointer;
  }
  
  .exit:hover {
    background-color: #fff;
  }
  
  .exit:active {
    background-color: wheat;
  }